/* Footer.css */
.footer {
  background: #282c34;
  color: rgba(255, 255, 255, 0.7);
  padding: 1.5rem;
  text-align: center;
  font-size: clamp(0.8rem, 2vw, 0.9rem);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer__content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer__text {
  margin: 0;
  line-height: 1.5;
}

.footer__image {
  margin: 0 -5px;
  width: 1rem;
  vertical-align: middle;
}

.footer__social {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.footer__social a {
  color: white;
  margin: 0 5px;
  text-decoration: none;
  transition: opacity 0.3s;
  display: inline-block;
}

.footer__social a:hover {
  opacity: 0.8;
  text-decoration: underline;
}

@media (max-width: 480px) {
  .footer {
    padding: 1rem;
  }
  
  .footer__social {
    gap: 0.25rem;
  }
  
  .footer__social a {
    margin: 0 3px;
  }
}