/* App.css */
/* Reset margins/padding and set base font */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: #333;
}

/* Full-height layout */
.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Main content grows to push footer down */
.main {
  flex: 1;
  padding: 2rem 1rem;
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
  text-align: center;
}

.main__placeholder {
  font-size: clamp(1.2rem, 2.5vw, 1.5rem);
  color: #555;
  padding: 10px 15px;
}

@media (max-width: 768px) {
  .main {
    padding: 1rem;
  }
}